
.headerPos{
	width:100%;
	position:absolute;
	left:0;
	top:40px;
}
#rule .pageFooter{
	width:100%;
	height:355px;
	background-position:center;
	background-size:1920px 355px;
}
#rule .pageHeader{
	width: 100%;
	height:498px;
	background-position:center;
	background-repeat:no-repeat;
	background-size:1920px 500px;
	display:table;
}
#rule .pcPageHeaderText{
	width:100%;
	max-width:1000px;
	display:table-cell;
	vertical-align:middle;
}
#rule .pcPageHeaderText>li{
	width:251px;
	height:117px;
	margin:0 auto;
	background-size:251px 117px;
	background-position:center;
	background-repeat:no-repeat;
	color:#fff;
	font-size:30px;
	line-height:80px;
}
#rule .page2{
	width:100%;
	background-position:top center;
	background-size:100% 100%;
	display:table;
	padding-top:145px;
	position:relative;
}
#rule .page2>div{
	display:table-cell;
	width:100%;
	max-width:1000px;
	vertical-align:middle;
}
#rule .page2Content{
	display:table;
	width:100%;
}
#rule .page2Content>li{
	display:table-cell;
	vertical-align:middle;
	width:50%;
}
#rule .page2Content>li>div{
	width:352px;
	float:left;
}
#rule .page2Content>li>div>p{
	margin-bottom:10px;
}
#rule .colorYellow{
	color:#e9c564;
}
#rule .page2>.underLine{
	display:block;
	bottom:-58px;
	left:50%;
	margin-left:-265.5px;
	position:absolute;
}
#rule .page3{
	width:100%;
	background-position:top center;
	background-size:100% 100%;
	display:table;
	padding-top:145px;
	position:relative;
}
#rule .page3Content>li>div{
	width:352px;
	float:right;
}
#rule .page3Content>li>div>p{
	margin-bottom:10px;
}
#rule .page3>.underLine{
	display:block;
	bottom:-182px;
	left:50%;
	margin-left:-380.5px;
	position:absolute;
}



