
#footer{
	width:100%;
	height:100%;
	text-align:center;
}
#footer>ul{
	display:table;
	width:100%;
	max-width:1000px;
	margin:0 auto;
}
#footer>ul>li{
	display:table-cell;
	vertical-align:middle;
	width:33.33%;
}
.footerTop{
	height:80%;
}
.footerBottom{
	height:20%;
	color:#7F7F7F;
}
#footer>.footerBottom>li{
	vertical-align:bottom;
}



